var that;
export default {
	data() {
		return {
			lodstatus:'',
			sort: '',
			sort_type: '-1',
			goods_list: [],
			cat_id: '',
			page: 1,
			goods_id: '',
			nomore:false,
			nolist:false
		};
	},
	methods: {
		qie(e) {
			let index = e.currentTarget.dataset.index;
			that.sort = index;
			if (index == 2) {
				if (that.sort_type != -1) {
					that.sort_type == 1 ? (that.sort_type = 0) : (that.sort_type = 1);
				} else {
					that.sort_type = 1;
				}
			} else {
				that.sort_type = '-1';
			}
			that.page = 1;
			that.getlist();
		},
		getlist() {
			let goods_list=that.goods_list;
			let page=that.page; 
			that.lodstatus = 'loading'; 
			let nolist='';
			let nomore='';
			that.$request
				.request({
					url: that.api.default.goods_list,
					data: {
						cat_id: that.cat_id,
						page: that.page,
						sort: that.sort,
						sort_type: that.sort_type,
						goods_id: that.goods_id
					}
				})
				.then(res => {
					console.log(res);
					let li=res.list;
					if(page==1){
						goods_list=li
						page=1
						goods_list.length==0?nolist=true:nolist=false,
						li.length==0?(that.lodstatus = '',that.nolist=true):(li.length>10?that.lodstatus = 'more':that.lodstatus = '')
					}else{
						if(li.length>0){
							goods_list=goods_list.concat(li);
							page++
							that.lodstatus = 'more';
						} else {
							that.lodstatus = 'noMore';
							that.nolist=true 
						}
					}
					that.goods_list=goods_list;
					that.page=page;
					that.nolist=nolist;
					that.nomore=nomore;
				});
		}
	},
	onLoad(option) {
		that = this;
		that.cat_id = option.cat_id ? option.cat_id : 5;
		that.getlist();
	},
	onReachBottom() {
		that.getlist();
	}
};
